body {
	font-family: Arial, sans-serif;
	background-color: #f4f4f4;
	margin: 0;
	padding: 0;
}

.app {
	max-width: 1200px;
	margin: 0;
	padding: 0;
}

main {
	display: flex;
	justify-content: center;
	padding: 20px;
}

header {
	background-color: #485e57;
	color: white;
	padding: 20px;
	text-align: center;

	h1 {
		margin: 0;
		font-size: 2.5rem;
	}
}

.footer {
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	width: 100%;
}
